import React from 'react'
import Proptypes from 'prop-types'
import { BdsIcon } from 'blip-ds/dist/blip-ds-react';

export const PageHeaderComponent = ({ title, logo }) => (
    <>

        <div className="header">
            <div className="dt dt--fixed">
                <div className="dtc tl v-mid w-90" style={{ display: 'flex', justifyContent: 'center', marginTop: '27px' }}>
                    <img src='https://raw.githubusercontent.com/Wilkor/img-clonebots/main/logoParseHub.jpeg' style={{ marginTop: '-26px', marginLeft:'60px', width: '36px', position:'fixed'}}></img>
                    <h1 className="bp-fs-4 bp-c-city" style={{ marginTop: '0px' }}></h1>
                </div>
                
            </div>
            <div className="dt dt--fixed">
                <div className="bp-divider-h"></div>
            </div>
        </div>
        


    </>
)

PageHeaderComponent.propTypes = {
    title: Proptypes.string,
}
