import React, { useEffect, useState } from 'react';

import Card from '../Cards/cards'

function TemplatePreview({ data, templateValue }) {



  return (

    <>
      <div class="left">
        <div class="blip-card-container">
          <div class="blip-card">
      
             {
            data.value[0].type === 'HEADER' ? (
                  
                  <Card data={data.value[1].text.replace(/\\n/g, "\n").replace(/(\r\n|\n|\r)/gm, "\n")} />
                  
                ) : (
                  <>
                  <div class="blip-container plain-text">
              <div class="bubble left">
                {
                  data.value.length > 1 ? (<>
                    <div>{data.value[0].text}</div>
                    <div class="mt3 mb0 flex flex-wrap items-center justify-around bt bp-bc-neutral-medium-wave">

                      {
                        data.value !== 'No message selected' ?
                          data.value.filter(e => e.type === 'BUTTONS').map((b) => b.buttons.map((btn, idx) => {

                            return (
                              <>
                                <div key={idx} class="mt3 w-40 pa2 flex justify-center bp-bg-neutral-whisper br3 pa2">
                                  <span class="f6 bp-c-neutral-desk">{btn.text}</span>
                                </div>
                              </>
                            )

                          })) : (
                            <div>Nenhum template selecionado</div>
                          )
                      }
                    </div>
                  </>) : (
                    <div>{data.value[0].text.replace(/\\n/g, "\n").replace(/(\r\n|\n|\r)/gm, "\n")}</div>
                  )

                }


              </div>
            </div>
                  </>
                )

            }

            
          </div>
        </div>
      </div>
    </>

  )

}


export default TemplatePreview;